html {
  color: #616161;
  font-family: "PT Sans", sans-serif;
  font-size: 14px; }

body,
ul,
ol {
  margin: 0;
  padding: 0;
  list-style: none; }

body {
  position: relative; }

*,
*::before,
*::after {
  box-sizing: border-box;
  font-variant-numeric: lining-nums; }

fieldset {
  border: none; }

input::-moz-placeholder {
  opacity: 1; }

input::-webkit-input-placeholder {
  opacity: 1; }

input:-ms-input-placeholder {
  opacity: 1; }

input::placeholder {
  opacity: 1; }

input[type='number'] {
  -moz-appearance: textfield; }
  input[type='number']::-webkit-outer-spin-button, input[type='number']::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0; }

select {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none; }

select::-ms-expand {
  display: none; }

h1,
p {
  margin-top: 0; }

@font-face {
  font-family: 'PT Sans';
  src: local("PT Sans Italic"), local("PTSans-Italic"), url("../assets/fonts/subset-PTSans-Italic.woff2") format("woff2"), url("../assets/fonts/subset-PTSans-Italic.woff") format("woff");
  font-weight: normal;
  font-style: italic;
  font-display: swap; }

@font-face {
  font-family: 'PT Sans';
  src: local("PT Sans"), local("PTSans-Regular"), url("../assets/fonts/subset-PTSans-Regular.woff2") format("woff2"), url("../assets/fonts/subset-PTSans-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: 'PT Sans';
  src: local("PT Sans Bold"), local("PTSans-Bold"), url("../assets/fonts/subset-PTSans-Bold.woff2") format("woff2"), url("../assets/fonts/subset-PTSans-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: 'PT Sans';
  src: local("PT Sans Bold Italic"), local("PTSans-BoldItalic"), url("../assets/fonts/subset-PTSans-BoldItalic.woff2") format("woff2"), url("../assets/fonts/subset-PTSans-BoldItalic.woff") format("woff");
  font-weight: bold;
  font-style: italic;
  font-display: swap; }

.container {
  max-width: 1171px;
  margin-right: auto;
  margin-left: auto;
  padding-right: 30px;
  padding-left: 30px; }

@media screen and (max-width: 443px) {
  .container {
    padding-right: 15px;
    padding-left: 15px; } }

.logo {
  display: inline-block;
  text-decoration: none; }
  .logo__img {
    width: 263px;
    height: 78px; }

.header {
  padding-top: 45px;
  padding-bottom: 37px; }

.services-list {
  display: flex; }
  .services-list__column {
    display: flex;
    flex-direction: column;
    width: calc((100% - 2 * 30px) / 3); }
    .services-list__column:nth-last-child(n + 1) {
      margin-right: 30px; }
  .services-list__item {
    margin-bottom: 20px; }

.hidden-block {
  height: 1000px;
  max-height: 0px;
  background-color: teal; }

.service-card {
  display: block;
  position: relative;
  padding-top: 80%;
  background-color: salmon;
  border-radius: 10px;
  box-shadow: 0 14px 38px 0 rgba(0, 0, 0, 0.5);
  overflow: hidden;
  text-decoration: none; }
  .service-card__content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    max-height: 100%;
    padding: 15px 30px 30px;
    transition: max-height 0.3s; }
  .service-card .service-card-name {
    flex-shrink: 0;
    width: 100%;
    margin-bottom: 20px; }
  .service-card .service-card-img {
    flex-shrink: 0;
    max-width: 100%;
    margin-bottom: 15px; }
    .service-card .service-card-img_cover {
      max-width: calc(100% + 2 * 30px); }
  .service-card:hover {
    padding-top: 0; }
    .service-card:hover .service-card__content {
      position: relative;
      height: auto;
      max-height: 5000px; }
    .service-card:hover .hidden-block {
      max-height: 500px; }
  .service-card_size_l {
    padding-top: 130%; }
  .service-card_size_m {
    padding-top: 115%; }

.service-card-name {
  max-width: 100%;
  color: #383838;
  font-size: 24px;
  font-weight: 700; }

@media screen and (max-width: 443px) {
  .service-card-name {
    font-size: 16px; } }

.service-card-img {
  object-fit: contain; }
  .service-card-img_cover {
    margin-top: -15px;
    margin-right: -30px;
    margin-left: -30px;
    object-fit: cover; }

.button {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 57px;
  padding-top: 12px;
  padding-right: 60px;
  padding-bottom: 12px;
  padding-left: 60px;
  border: none;
  background-color: #e06061;
  border-radius: 28px;
  color: #ffffff;
  font-size: 20px;
  font-weight: 800;
  text-align: center;
  cursor: pointer; }

@media screen and (max-width: 443px) {
  .button {
    min-height: 48px;
    font-size: 16px; } }

.products-list__item:nth-last-child(n + 2) {
  margin-bottom: 20px; }

.product {
  display: flex;
  text-decoration: none;
  opacity: 0.4; }
  .product__icon {
    flex-shrink: 0;
    width: 18px;
    height: 18px;
    margin-right: 20px;
    color: inherit; }
  .product__text {
    line-height: 1.25; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  bottom: 0;
  overflow: hidden;
  clip: rect(0 0 0 0); }

.overflow_hidden {
  overflow: hidden; }

.overflow-y_auto {
  overflow-y: auto; }

.overflow-y_visible {
  overflow-y: visible; }

.overflow-y_hidden {
  overflow-y: hidden; }

.swiper-wrapper > * {
  flex-shrink: 0; }
